<template>
    <div>
        <transition name="slide">
            <div class="modal" v-show="showModal">
            <div class="mask"></div>
            <div class="modal-dialog">
                <div class="modal-header">
                    <span>{{title}}</span>
                    <a href="javascript:;" class="icon-close" @click="$emit('cancle')"></a>
                </div>
                <div class="modal-body">
                    <slot name="body"></slot>
                </div>
                <div class="modal-footer">
                    <a href="javascript:;" class="btn" v-if="btnType==1" @click="$emit('submit')">{{sureText}}</a>
                     <a href="javascript:;" class="btn" v-if="btnType==2" @click="$emit('cancle')">{{cancelText}}</a>
                    <div class="btn-group" v-if="btnType==3">
                        <a href="javascript:;" class="btn" @click="$emit('submit')">{{sureText}}</a>
                     <a href="javascript:;" class="btn btn-default"  @click="$emit('cancle')">{{cancelText}}</a>
                    </div>
                    
                </div>
            </div>
        </div>
        </transition>
        
    </div>
</template>

<script>
    export default{
        name:'modal',
        props:{
            modalType:{
                //smallmiddlelargeform
                type:String,
                default:'form'
            },
            //
            title:String,
            // 1 2 3
            btnType:String,
            sureText:{
                type:String,
                default:''
            },
            cancelText:{
                type:String,
                default:''
            },
            showModal:Boolean
        }
    }
</script>
<style lang="scss" >
    @import './../assets/scss/mixin.scss';
    @import './../assets/scss/config.scss';
    @import './../assets/scss/modal.scss';
</style>